.requisite {
  padding: 0 40px 18px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;

  &__item {
    display: block;
    padding: 10px 0;
    span {
      display: block;
    }
  }

  &__item-input {
    width: 100%;
    padding: 8px;
    margin-top: 15px;
    text-align: center;
    text-transform: uppercase;

    &--short {
      width: 50%;
    }
  }

}